.footer__container {
  @include tablet {
    display: flex;
    justify-content: space-between;
  }
  @include desktop {
    justify-content: baseline;
  }
}
.footer__name {
  display: block;
  text-align: left;
  margin-top: 40px;
  margin-bottom: 40px;
  @include desktop {
    margin-top: 121px;
    margin-bottom: 60px;
  }
}
.footer__text {
  width: 232px;
  margin-bottom: 16px;
  font-family: 'Titan One', cursive;
  font-weight: 400;
  font-size: 18px;
  letter-spacing: 0.04em;
  line-height: 1.2;
  color: #ffa5ba;
  @include tablet {
    width: 340px;
    margin-bottom: 12px;
    font-size: 26px;
    letter-spacing: 0.04em;
  }
  @include desktop {
    width: 472px;
    font-size: 30px;
    margin-bottom: 16px;
  }
}
.footer__title {
  width: 200px;
  font-family: 'Titan One', cursive;
  font-weight: 400;
  font-size: 34px;
  line-height: 1.2;
  letter-spacing: 0.04em;
  color: $primary-accent-color;
  @include tablet {
    width: 340px;
    font-size: 48px;
    letter-spacing: 0.06em;
  }
  @include desktop {
    font-size: 58px;
    margin-right: 0px;
    width: 100%;
  }
}
.footer__contacts {
  text-align: right;
  @include tablet {
    margin-top: 100px;
  }
  @include desktop {
    margin-top: 132px;
    margin-bottom: 60px;
  }
}
.footer__contacts-box {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 40px;
}
.footer__contacts-soc {
  display: flex;
  margin-top: 0px;
  margin-bottom: 0px;
}
.footer__contacts-item {
  width: 32px;
  height: 32px;
  list-style: none;

  &:not(:last-child) {
    margin-right: 28px;
  }
  @include tablet {
    width: 34px;
    height: 34px;
  }
}
.footer__contacts-link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background-color: #fea5bb;

  &:hover,
  &:focus {
    transition-property: background-color;
    transition-duration: 250ms;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    background-color: $primary-accent-color;
  }

  &:hover .footer__contacts--icon,
  &:focus .footer__contacts--icon {
    fill: #ffffff;
  }
}
.footer__contacts-icon {
  fill: #fff5f6;
}
.footer__contacts-tel {
  display: flex;
  align-items: baseline;
  flex-wrap: nowrap;
  justify-content: flex-end;
  margin-bottom: 40px;
}
.footer__contacts-text {
  margin-right: 4px;
  font-family: inherit;
  font-weight: 700;
  font-size: 13px;
  line-height: 1.9;
  color: $primary-text-color;
  @include tablet {
    font-size: 14px;
    letter-spacing: 0.02em;
  }
  @include desktop {
    font-size: 16px;
  }
}
.footer__contacts-number {
  font-family: inherit;
  font-weight: 700;
  font-size: 12px;
  line-height: 1.9;
  color: $primary-accent-color;
  @include tablet {
    font-weight: 500;
    font-size: 14px;
    letter-spacing: 0.02em;
  }
}
.footer__cooperation {
  text-align: center;
  padding-top: 16px;
  padding-bottom: 3px;
  border-top: 1px solid #e1e1e1;
  @include tablet {
    display: flex;
    align-items: center;
    justify-content: center;
    padding-top: 8px;
    padding-bottom: 4px;
  }
  @include desktop {
    padding-top: 7px;
    padding-bottom: 4px;
  }
}
.footer__cooperation-text {
  font-family: inherit;
  font-weight: 700;
  font-size: 14px;
  line-height: 1.5;
  color: $secondary-text-color;
  @include tablet {
    margin-right: 3px;
    line-height: 1.9;
    letter-spacing: 0.02em;
  }
}
.footer__cooperation-row {
  font-family: inherit;
  font-weight: 700;
  font-size: 14px;
  line-height: 1.5;
  color: $secondary-text-color;
  @include tablet {
    line-height: 1.9;
    letter-spacing: 0.02em;
  }
}
